@font-face {
    font-family:"NotoSans-Bold";
    src: url("../assets/fonts/NotoSans-Bold.ttf");
}
@font-face {
    font-family:"NotoSans-Regular";
    src: url("../assets/fonts/NotoSans-Regular.ttf");
}
.task{
    
    border-radius:5px;
    width:230px;
    cursor: pointer;
    padding:12px;
    display:flex;
    flex-direction:column;
    align-content:center;
    justify-content:center;
    color:rgb(255, 255, 255);
    font-family:"NotoSans-Regular";
    cursor: pointer;
    transition: all 0.1s;
    margin-bottom:6px;
    margin-right:6px;
    animation-name:entry;
    animation-duration:0.2s;
    animation-timing-function:ease-in-out;
    animation-iteration-count:1;
}
@keyframes entry{
    0%{transform:scale(0.6);}
    100%{transform:scale(1);}
}
.task:hover{
    background-color:rgba(255, 255, 255, 0.041);
    transform:scale(0.9);
}
.todo{
    display:flex;
    flex-direction:row;
    justify-content:start;
    align-items:center;
}
.txt-val p{
  width:180px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color:white;
}
.txt-val-lgt p{
    width:180px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color:rgba(0, 0, 0, 0.445);
    font-family:"NotoSans-Bold";

}
.close{
    background-color:rgb(255, 255, 255,.1);
    padding:5px;
    width:20px;
    height:20px;
    display:flex;
    align-items:center;
    justify-content:center;
    border-radius:6px;
    margin-left:20px;
    transition:all 0.2s;
}
.close-lgt{
    background-color:rgba(0, 0, 0, 0.123);
    padding:5px;
    width:20px;
    height:20px;
    display:flex;
    align-items:center;
    justify-content:center;
    border-radius:6px;
    margin-left:20px;
    transition:all 0.2s;
}
.close:hover{
    transform:scale(1.2);
    color:rgba(245, 245, 245, 0.233);
}
.close-lgt:hover{
    transform:scale(1.2);
}
.date{
    color:rgba(245, 245, 245, 0.13);
}
.date-lgt{
    color:rgba(0, 0, 0, 0.493);
}