@font-face {
    font-family:"NotoSans-Bold";
    src: url("../assets/fonts/NotoSans-Bold.ttf");
}
@font-face {
    font-family:"NotoSans-Regular";
    src: url("../assets/fonts/NotoSans-Regular.ttf");
}

.Badge{
    display:flex;
    flex-direction:row;
    justify-content:start;
    align-items:center;
    background-color:rgb(255,255,255,.1);
    border-radius:6px;
    width:max-content;
    height:34px;
    padding:3px;
}
.Badge p{
    margin-left:5px;
    margin-right:5px;
    font-family:"NotoSans-Bold";
}
.Badge-lgt{
    display:flex;
    flex-direction:row;
    justify-content:start;
    align-items:center;
    background-color:rgba(0, 0, 0, 0.062);
    border-radius:6px;
    width:max-content;
    height:34px;
    padding:3px;
}
.Badge-lgt p{
    color:grey;
    margin-left:5px;
    margin-right:5px;
    font-family:"NotoSans-Bold";
}
.circle{
    border-radius:5px;
    padding:3px;
    margin-left:3px;
    width:20px;
    height:20px;
    color:white;
    display:flex;
    justify-content:center;
    align-items:center;
    font-family:"NotoSans-Bold";
}