.app{
  width:100%;
  height:100vh;
  user-select:none;
  background-color:transparent;
}
.dark{
  animation-name:DarkMode;
  animation-duration: 4s;
  animation-delay: -2s;
  background-color:#23262C;
}

.light{
  /*animation-name:LightMode;
  animation-duration: 4s;
  animation-delay: -2s;*/
  background-color:whitesmoke;
}
@keyframes dark_md{
  from {background-color:whitesmoke;}
  to {background-color:#23262C;}
}
@keyframes light_md{
  from {background-color:#23262C;}
  to {background-color:whitesmoke;}
}

.topBar{
  display:flex;
  flex-direction:row;
  padding:43px;
}
@media screen and (max-width: 400px) {
  .h-sub{
    margin-bottom:10px;
  }
  .topBar{
    padding:18px;
  }
}
.task-place{
  display:flex;
  flex-direction:row;
  flex-wrap: wrap;
  align-items:center;
  justify-content:flex-start;
  padding-left:30px;
}
.context-modal{
  position:fixed;
  width:100vw;
  height:100vh;
  display:flex;
  align-items:flex-end;
  justify-content:flex-end;
  background-color:rgba(0, 0, 0, 0.397);
  transition:all .5s ease;

}
@media screen and (max-width: 450px) {
  .context-modal{
    align-items:center;
    justify-content:center;
  }
  .task-place{
    align-items:center;
    justify-content:center;
    padding-left:0px;
  }
}
@font-face {
  font-family:"NotoSans-Bold";
  src: url("./assets/fonts/NotoSans-Bold.ttf");
}
@font-face {
  font-family:"NotoSans-Regular";
  src: url("./assets/fonts/NotoSans-Regular.ttf");
}


.no-task{
  color:rgba(255, 255, 255, 0.253);
  font-family:"NotoSans-Regular";
  font-size:24px;
  position:absolute;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  z-index:-2;
  width:max-content;
}

.no-task-lgt{
  color:rgba(128, 128, 128, 0.466);
  font-size:24px;
  position:absolute;
  font-family:"NotoSans-Regular";
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  z-index:-2;
  width:max-content;
}
@media screen and (max-width: 400px) {
  .no-task,.no-task-lgt{
  
      font-size:17px;

  }
}