.add{
    width:50px;
    height:50px;
    color:whitesmoke;
    font-size:27px;
    border:none;
    border-radius:17px;
    cursor:pointer;
    transition:0.2s ease;
    font-weight:lighter;
    padding:10px;
    margin-top:45px;
    margin-right:23px;
}
.add:hover{
    transform:scale(0.8);
}
@media screen and (max-width: 400px) {
    .add{
        width:40px;
        height:40px;
        font-size:20px;
        margin-right:18px;
        border-radius:7px;
    }
}