.modal{
    width:300px;
    height:300px;
    background-color:#18191d;
    border-radius:10px;
    margin-right:50px;
    margin-bottom:50px;
    animation-name:entry;
    animation-duration:0.2s;
    animation-timing-function:ease-in-out;
    animation-iteration-count:1;
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
  }
  @media screen and (max-width: 450px) {
    .modal{
      margin-right:0px;
      margin-bottom:0px;
    }
  }
  @keyframes entry{
    0%{transform:scale(0.6);}
    100%{transform:scale(1);}
  }
  @font-face {
    font-family:"NotoSans-Bold";
    src: url("../assets/fonts/NotoSans-Bold.ttf");
  }
  @font-face {
    font-family:"NotoSans-Regular";
    src: url("../assets/fonts/NotoSans-Regular.ttf");
  }
  
  .modal p:nth-child(1){
    font-family:"NotoSans-Regular";
    color:rgba(245, 245, 245, 0.11);
    padding-bottom:12px;
  }
  .header-top{
    display:flex;
    flex-direction:row;
    justify-content:flex-start;
    align-items:center;
}

  .modal p:nth-child(1){
    font-family:"NotoSans-Regular";
    color:rgb(245, 245, 245);
    height:0px;
    font-size:18px;
  }
  
  .modal p:nth-child(2){
    font-family:"NotoSans-Regular";
    font-size:13px;
    color:rgba(245, 245, 245, 0.11);
    height:0px;
  }
  .modal p:nth-child(3){
    font-family:"NotoSans-Regular";
    font-size:15px;
    color:rgba(245, 245, 245, 0.11);
  }
  
  .task_input{
    background-color:transparent;
    border:1px solid rgba(245, 245, 245, 0.062);
    padding:12px;
    border-radius:6px;
    color:white;
    font-family:"NotoSans-Regular";
    outline:none;
    width:200px;
  
  }
  .task_input:focus{
    outline:none;
  }

.add-modal{
    width:max-content;
    height:50px;
    color:rgba(245, 245, 245, 0.226);
    font-size:14px;
    background-color:rgba(245, 245, 245, 0.048);
    border:none;
    border-radius:7px;
    cursor:pointer;
    font-family:"NotoSans-Regular";
    transition:0.2s ease;
    z-index:122;
    display:inline;
    margin-top:12px;
}
.container-modal{
    display:flex;
    flex-direction:column;

}
.add-modal:hover{
    color:white;
    transform:scale(0.8);
}

.close-modal{
    background-color:rgb(255, 255, 255,.1);
    padding:5px;
    width:20px;
    height:20px;
    display:flex;
    flex-direction:row;
    align-items:center;
    justify-content:center;
    border-radius:6px;
    transition:all 0.2s;
    z-index:200;
    cursor: pointer;
    margin-left:50px;
}
.close-modal:hover{
    transform:scale(1.2);
    color:rgba(245, 245, 245, 0.233);
}